import React from 'react';
import { ControlProps } from '@jsonforms/core';
import BaseToggleButtonGroup from '../BaseToggleButtonGroup';

interface Props extends ControlProps {
  handleChange(path: string, value: string): void;
}

const ToggleButtonGroupRenderer = (props: Props) => {
  const { handleChange, path } = props;

  return (
    <BaseToggleButtonGroup
      {...props}
      multiselect={false}
      onChange={(value) => handleChange(path, value.value)}
    />
  );
};

export default ToggleButtonGroupRenderer;
