import React from 'react';
import { RendererProps } from '@jsonforms/core';
import BaseFormRow from '../BaseFormRow/BaseFormRow';
import { HorizontalFormRowLayout } from '../types';
import { renderChildren } from '../utils';

const HorizontalFormRowRenderer = ({
  schema,
  uischema,
  enabled,
  visible,
  path,
  renderers,
}: RendererProps) => {
  const layout = uischema as HorizontalFormRowLayout;

  return (
    <BaseFormRow visible={visible} direction="horizontal">
      {renderChildren(layout, schema, path, enabled, renderers)}
    </BaseFormRow>
  );
};

export default React.memo(HorizontalFormRowRenderer);
