import React, { useState } from 'react';
import { FormProcessContextState, FormProcessStep } from '../types';

interface Props {
  children: React.ReactNode;
}

export const FormProcessContext = React.createContext<FormProcessContextState>({
  step: FormProcessStep.Information,
  acknowledgeInformation: () => null,
});

const FormProcessProvider = ({ children }: Props) => {
  const [step, setStep] = useState(FormProcessStep.Information);

  function acknowledgeInformation() {
    setStep(FormProcessStep.DataInput);
  }

  const contextValue = {
    step,
    acknowledgeInformation,
  };

  return (
    <FormProcessContext.Provider value={contextValue}>
      {children}
    </FormProcessContext.Provider>
  );
};

export default FormProcessProvider;
