import { useJsonForms } from '@jsonforms/react';
import React from 'react';
import { InfoCard } from '@dnb/eufemia';
import { information_bubble_medium } from '@dnb/eufemia/icons';
import { ControlProps } from '@jsonforms/core';
import { translateText } from '../../i18n';
import { InfoCardControl } from '../types';

const InfoCardRenderer = (props: ControlProps) => {
  const jsonFormsContext = useJsonForms();

  const { uischema, visible } = props;

  if (!visible) return null;

  const layout = uischema as InfoCardControl;

  return (
    <InfoCard
      text={translateText(
        jsonFormsContext.i18n,
        layout.options.i18n?.text,
        layout.options.text
      )}
      title={translateText(
        jsonFormsContext.i18n,
        layout.options.i18n?.title,
        layout.options.title
      )}
      icon={information_bubble_medium}
    />
  );
};
export default InfoCardRenderer;
