import { withJsonFormsMultiEnumProps } from '@jsonforms/react';
import React, { FC, useState } from 'react';
import {
  ControlProps,
  OwnPropsOfEnum,
  DispatchPropsOfMultiEnumControl,
} from '@jsonforms/core';
import ToggleCollection, {
  CollectionItem,
} from '../../../ToggleCollection/ToggleCollection';

const ToggleCollectionControl: FC<
  ControlProps & OwnPropsOfEnum & DispatchPropsOfMultiEnumControl
> = (props) => {
  const [items, setItems] = useState<CollectionItem[]>([]);

  const autoCompleteOptions = props.options?.map((option) => {
    return {
      content: option.label,
      value: option.value,
    };
  });

  const handle = (event) => {
    props.handleChange(props.path, event.values);
  };

  const handleNewItem = (item) => {
    const exists = items.find((i) => i.value === item.value);

    if (!exists) {
      setItems([...items, item]);
      props.addItem(props.path, item.value);
    }
  };

  return (
    <ToggleCollection
      items={items}
      values={props.data}
      buttonText={props.label}
      dataList={autoCompleteOptions}
      handleChange={handle}
      handleNewItem={handleNewItem}
    />
  );
};

export default withJsonFormsMultiEnumProps(ToggleCollectionControl);
