import React from 'react';
import { Icon as EufemiaIcon } from '@dnb/eufemia';
import * as icons from '@dnb/eufemia/icons';

interface Props {
  name?: keyof typeof icons;
  color?: 'sea-green' | 'white';
}

const Icon = ({ name, color }: Props) => {
  if (!name) return null;

  const eufemiaIcon = icons[name];

  if (!eufemiaIcon) return null;

  return (
    <EufemiaIcon
      icon={eufemiaIcon}
      right="x-small"
      height="22"
      color={color ? `var(--color-${color})` : undefined}
      data-testid="icon"
    />
  );
};

export default Icon;
