import { useJsonForms } from '@jsonforms/react';
import React from 'react';
import { Div, P } from '@dnb/eufemia';
import { RendererProps } from '@jsonforms/core';
import { translateText } from '../../i18n';
import { CheckboxGroup } from '../types';
import { renderChildren } from '../utils';
import styles from './CheckboxGroup.module.scss';

const CheckboxGroupRenderer = ({
  schema,
  uischema,
  enabled,
  path,
  visible,
  renderers,
}: RendererProps) => {
  const jsonFormsContext = useJsonForms();

  if (!visible) return null;

  const layout = uischema as CheckboxGroup;
  const { i18n, label } = layout;

  return (
    <Div className={styles.checkboxGroup}>
      <P
        className="dnb-typo-medium"
        bottom="small"
        data-testid="checkbox-group-title"
      >
        {translateText(jsonFormsContext.i18n, i18n, label)}
      </P>
      <div className={styles.container}>
        {renderChildren(layout, schema, path, enabled, renderers)}
      </div>
    </Div>
  );
};

export default React.memo(CheckboxGroupRenderer);
