import React from 'react';
import { Input } from '@dnb/eufemia';
import { ControlProps } from '@jsonforms/core';
import BaseFormRow from '../../layouts/BaseFormRow';

const TextControlRenderer = (props: ControlProps) => {
  const { label, handleChange, path, visible, data, errors } = props;

  return (
    <BaseFormRow visible={visible}>
      <Input
        data-testid={path || 'text-input'}
        label_direction="vertical"
        stretch
        size="medium"
        label={label}
        on_change={onChange}
        value={data}
        status={errors}
      />
    </BaseFormRow>
  );

  function onChange({ value }: { value: string }) {
    handleChange(path, value || undefined);
  }
};
export default TextControlRenderer;
