import React from 'react';
import { ToggleButton } from '@dnb/eufemia/components';
import { ControlProps, OwnPropsOfEnum } from '@jsonforms/core';
import BaseFormRow from '../../layouts/BaseFormRow';

interface Props extends ControlProps, OwnPropsOfEnum {
  onChange?: (...args: any[]) => any;
  variant?: 'checkbox';
  multiselect: boolean;
}

const BaseToggleButtonGroupRenderer = (props: Props) => {
  const {
    onChange,
    visible,
    label,
    variant,
    multiselect,
    data,
    errors,
    options,
  } = props;

  return (
    <BaseFormRow visible={visible}>
      <ToggleButton.Group
        value={data ? data : null}
        values={data ? data : []}
        label={label}
        variant={variant}
        multiselect={multiselect}
        data-testid="toggle-button-group"
        label_direction="vertical"
        layout_direction="row"
        on_change={onChange}
        status={errors}
      >
        {options?.map(({ label, value }, i) => (
          <ToggleButton key={i} text={label} value={value} />
        ))}
      </ToggleButton.Group>
    </BaseFormRow>
  );
};
export default BaseToggleButtonGroupRenderer;
