import { JsonForms } from '@jsonforms/react';
import { JsonFormsInitStateProps } from '@jsonforms/react';
import React from 'react';
import { useLanguage } from '@dnb/locale';
import { JsonSchema, UISchemaElement, JsonFormsCore } from '@jsonforms/core';
import { renderers as defaultRenderers } from './configuration';
import { FormContext } from './context';
import { errorTranslator, getTranslation } from './i18n';

export interface Props {
  schema: JsonSchema;
  uiSchema: UISchemaElement;
  data: Record<string, any>;
  onDataChange: (
    data: JsonFormsCore['data'],
    errors: JsonFormsCore['errors']
  ) => void;
  onSave: (
    data: JsonFormsCore['data'],
    errors: JsonFormsCore['errors']
  ) => void;
  errors?: JsonFormsInitStateProps['additionalErrors'];
  actions?: Record<string, (args?: any[]) => void>;
  renderers?: JsonFormsInitStateProps['renderers'];
  'data-testid'?: string;
}

type DispatchFunction = {
  (data: JsonFormsCore['data'], errors: JsonFormsCore['errors']): void;
};

const Form = ({
  schema,
  uiSchema,
  data,
  onDataChange,
  onSave,
  errors,
  actions = {},
  renderers = [],
  'data-testid': dataTestId,
}: Props) => {
  const { locale } = useLanguage();

  const formActions: Record<string, DispatchFunction> = {
    save: onSave,
    ...actions,
  };

  function dispatch(
    actionName: string,
    jsonFormsCore: JsonFormsCore | undefined
  ) {
    const action = formActions[actionName];

    if (!action) return;

    const { data, errors } = jsonFormsCore || { data: null };

    action(data, errors);
  }

  function translator(key: string, defaultMessage: string) {
    return getTranslation(locale, key, defaultMessage);
  }

  function handleChange(state: Pick<JsonFormsCore, 'data' | 'errors'>) {
    onDataChange(state.data, state.errors);
  }

  return (
    <FormContext.Provider value={{ dispatch }}>
      <div data-testid={dataTestId || 'form'}>
        <JsonForms
          schema={schema}
          uischema={uiSchema}
          renderers={defaultRenderers.concat(renderers || [])}
          data={data}
          onChange={handleChange}
          i18n={{
            locale,
            translate: translator,
            translateError: errorTranslator,
          }}
          additionalErrors={errors}
        />
      </div>
    </FormContext.Provider>
  );
};

export default Form;
