import { useJsonForms } from '@jsonforms/react';
import React from 'react';
import { ControlProps } from '@jsonforms/core';
import { translationKeys } from '@pm-netbank/translations';
import { translateText } from '../../i18n';
import styles from './ControlSummary.module.scss';

const ControlSummaryRenderer = (props: ControlProps) => {
  const jsonFormsContext = useJsonForms();

  const { visible, label, data, path } = props;

  if (!visible) return null;

  return (
    <div
      className={styles.propertyContainer}
      data-testid={path || 'control-summary'}
    >
      <span
        data-testid="control-summary-label"
        className={styles.propertyLabel}
      >
        {label}
      </span>
      <span data-testid="control-summary-value">
        {normalizeDataValue(data)}
      </span>
    </div>
  );

  function normalizeDataValue(value: any) {
    switch (typeof value) {
      case 'boolean': {
        const booleanValueTranslation = value
          ? translationKeys.forms.forms_boolean_true
          : translationKeys.forms.forms_boolean_false;

        return translateText(jsonFormsContext.i18n, booleanValueTranslation);
      }
      default:
        return value;
    }
  }
};

export default ControlSummaryRenderer;
