import React, { useState, useRef, useEffect, FC } from 'react';
import { Button, Autocomplete, Icon } from '@dnb/eufemia';
import { add_circled } from '@dnb/eufemia/icons';
import styles from './AddByAutocomplete.module.scss';

export interface DataListItem {
  content: string;
  value: string;
}
export interface Props {
  /** Label text for the add button */
  buttonText: string;
  /** function to run on_change in autocomplete */
  onAutocompleteChange: (selection: DataListItem) => void;
  /** List of possible value in autocomplete  */
  dataList: DataListItem[];

  /** disable appear/disappear (show/hide) animation */
  no_animation?: boolean;
}

/**
 * Add button that uses autocomplete to select values to add
 */
const AddByAutocomplete: FC<Props> = ({
  dataList,
  buttonText,
  onAutocompleteChange,
  no_animation,
}) => {
  const [insert, setInsert] = useState<boolean>(false);

  const autocompleteRef = useRef<HTMLInputElement>();
  const addButtonRef = useRef<HTMLButtonElement>();

  const handleButtonClick = () => {
    setInsert(true);
  };

  const handleAutoCompleteChange = ({ data }) => {
    onAutocompleteChange(data);
    setInsert(false);
  };

  useEffect(() => {
    if (insert) {
      autocompleteRef.current.focus();
    } else {
      addButtonRef.current.focus();
    }
  }, [insert]);

  if (insert) {
    return (
      <Autocomplete
        class={styles.addByAutocomplete}
        input_ref={autocompleteRef}
        size="medium"
        right="medium"
        stretch
        placeholder={'Velg land'}
        data={dataList}
        label_direction="vertical"
        on_change={handleAutoCompleteChange}
        no_animation={no_animation}
      />
    );
  }

  return (
    <span>
      <Button
        innerRef={addButtonRef}
        inner_ref={addButtonRef}
        variant="secondary"
        class={styles.addButton}
        on_click={handleButtonClick}
        text={buttonText}
        icon={<Icon icon={add_circled} />}
        icon_position="left"
      />
    </span>
  );
};

export default AddByAutocomplete;
