import icons from '@dnb/eufemia/icons';
import { Layout, Lableable, Internationalizable } from '@jsonforms/core';

export enum LayoutType {
  CheckboxGroup = 'CheckboxGroup',
  Container = 'Container',
  Form = 'Form',
  Group = 'Group',
  HorizontalFormRowLayout = 'HorizontalFormRowLayout',
  Section = 'Section',
  Step = 'Step',
  SummaryContainer = 'SummaryContainer',
}

export interface StepLayout
  extends Layout,
    Lableable<string>,
    Internationalizable {
  type: LayoutType.Step;
}

export interface SectionLayout
  extends Layout,
    Lableable<string>,
    Internationalizable {
  type: LayoutType.Section;
  options?: {
    icon?: keyof typeof icons;
  };
}

export interface FormLayout extends Layout {
  type: LayoutType.Form;
  options: {
    mode: 'static' | 'strict' | 'loose';
  };
}

export interface ContainerLayout
  extends Layout,
    Lableable<string>,
    Internationalizable {
  type: LayoutType.Container;
  options?: {
    containerType: 'form' | 'stack';
  };
}

export interface GroupLayout extends Layout {
  type: LayoutType.Group;
  options: {
    title: string;
    subTitle?: string;
    i18n?: {
      title: string;
      subTitle?: string;
    };
  };
}
export interface HorizontalFormRowLayout
  extends Layout,
    Lableable<string>,
    Internationalizable {
  type: LayoutType.HorizontalFormRowLayout;
}

export interface CheckboxGroup
  extends Layout,
    Lableable<string>,
    Internationalizable {
  type: LayoutType.CheckboxGroup;
}
