import { useJsonForms } from '@jsonforms/react';
import React from 'react';
import { Div, P } from '@dnb/eufemia';
import { RendererProps } from '@jsonforms/core';
import { translateText } from '../../i18n';
import { GroupLayout } from '../types';
import { renderChildren } from '../utils';
import styles from './GroupLayout.module.scss';

const GroupLayoutRenderer = ({
  schema,
  uischema,
  enabled,
  path,
  visible,
  renderers,
}: RendererProps) => {
  const jsonFormsContext = useJsonForms();

  if (!visible) return null;

  const layout = uischema as GroupLayout;

  const hasTitle = layout.options?.title || layout.options?.i18n?.title;
  const hasSubTitle =
    layout.options?.subTitle || layout.options?.i18n?.subTitle;

  return (
    <Div className={styles.group}>
      {getTitle()}
      {getSubTitle()}
      <Div className={styles.innerGroup}>
        {renderChildren(layout, schema, path, enabled, renderers)}
      </Div>
    </Div>
  );

  function getTitle() {
    if (!hasTitle) return null;

    return (
      <P
        className="dnb-typo-medium"
        data-testid="group-title"
        bottom={hasSubTitle ? 'xx-small' : 'small'}
      >
        {translateText(
          jsonFormsContext.i18n,
          layout.options.i18n?.title,
          layout.options.title
        )}
      </P>
    );
  }

  function getSubTitle() {
    if (!hasSubTitle) return null;

    return (
      <P data-testid="group-subtitle" bottom="small" size="x-small">
        {translateText(
          jsonFormsContext.i18n,
          layout.options.i18n?.subTitle,
          layout.options.subTitle
        )}
      </P>
    );
  }
};

export default React.memo(GroupLayoutRenderer);
