import icons from '@dnb/eufemia/icons';
import { ControlElement } from '@jsonforms/core';

export enum ControlType {
  Button = 'Button',
  Checkbox = 'Checkbox',
  Control = 'Control',
  ControlSummary = 'ControlSummary',
  Dropdown = 'Dropdown',
  InfoCard = 'InfoCard',
  PhoneInput = 'PhoneInput',
  RadioGroup = 'RadioGroup',
  TextInput = 'TextInput',
  ToggleButtonGroup = 'ToggleButtonGroup',
  ToggleButtonGroupCheckbox = 'ToggleButtonGroupCheckbox',
}

export interface InfoCardControl extends ControlElement {
  options: {
    title?: string;
    text?: string;
    status: 'info';
    i18n?: {
      title: string;
      text: string;
    };
  };
}

export interface ButtonControl extends ControlElement {
  options: {
    action: string;
    icon?: keyof typeof icons;
  };
}
