import { useJsonForms } from '@jsonforms/react';
import React, { useMemo, useState } from 'react';
import { FormSet, StepIndicator } from '@dnb/eufemia/components';
import { Space } from '@dnb/eufemia/components';
import { Button } from '@dnb/eufemia/components';
import { StepIndicatorData } from '@dnb/eufemia/components/step-indicator/StepIndicator';
import { RendererProps, UISchemaElement } from '@jsonforms/core';
import { translationKeys } from '@pm-netbank/translations';
import { translateText } from '../../i18n';
import BaseLayout from '../BaseLayout/BaseLayout';
import { StepLayout, FormLayout } from '../types';
import { renderChildren } from '../utils';
import styles from './FormLayout.module.scss';

const SteppedLayoutRenderer = ({
  schema,
  uischema,
  enabled,
  visible,
  path,
  renderers,
}: RendererProps) => {
  const [currentStep, setCurrentStep] = useState(0);
  const jsonFormsContext = useJsonForms();

  const layout = uischema as FormLayout;

  const stepElements = useMemo(
    () => (layout.elements || []).filter((e) => e.type === 'Step'),
    [layout.elements]
  );

  if (!visible) return null;

  if (stepElements.length === 0)
    return (
      <FormSet>
        <BaseLayout>
          {renderChildren(layout, schema, path, enabled, renderers)}
        </BaseLayout>
      </FormSet>
    );

  const stepsIndicatorData: StepIndicatorData = stepElements.map(
    mapStepElementToEufemia
  );

  const currentStepLayout = stepElements[currentStep] as StepLayout;

  const currentStepActionButtons = currentStepLayout.elements.filter(
    ({ type }) => type === 'Button'
  );

  const currentStepElements = currentStepLayout.elements.filter(
    ({ type }) => type !== 'Button'
  );

  return (
    <FormSet className={styles.container}>
      <div data-testid="form-layout-sidebar">
        <StepIndicator.Sidebar sidebar_id="form-layout-sidebar" />
      </div>
      <Space stretch>
        <StepIndicator
          sidebar_id="form-layout-sidebar"
          data={stepsIndicatorData}
          mode={layout.options.mode}
          title=""
          current_step={currentStep}
          on_change={({ current_step }) => setCurrentStep(current_step)}
        />
        <BaseLayout>
          {renderChildren(
            { ...currentStepLayout, elements: currentStepElements },
            schema,
            path,
            enabled,
            renderers
          )}
        </BaseLayout>
        <div
          className={styles.buttonsContainer}
          data-testid="form-layout-controls"
        >
          {getGoToPreviousButton()}
          {getCustomActionButtons()}
          {getGoToNextButton()}
        </div>
      </Space>
    </FormSet>
  );

  function getGoToPreviousButton() {
    if (currentStep === 0) return null;

    return (
      <Button
        data-testid="form-layout-control-previous"
        variant="tertiary"
        icon="chevron_left"
        icon_position="left"
        onClick={() => setCurrentStep(currentStep - 1)}
      >
        {translateText(
          jsonFormsContext.i18n,
          translationKeys.forms.forms_step_previous
        )}
      </Button>
    );
  }

  function getGoToNextButton() {
    if (currentStep === stepElements.length - 1) return null;

    return (
      <Button
        data-testid="form-layout-control-next"
        variant="primary"
        icon="chevron_right"
        icon_position="right"
        onClick={() => setCurrentStep(currentStep + 1)}
      >
        {translateText(
          jsonFormsContext.i18n,
          translationKeys.forms.forms_step_next
        )}
      </Button>
    );
  }

  function getCustomActionButtons() {
    return renderChildren(
      { ...currentStepLayout, elements: currentStepActionButtons },
      schema,
      path,
      enabled,
      renderers
    );
  }

  function mapStepElementToEufemia(element: UISchemaElement) {
    const elementLayout = element as StepLayout;

    return (
      translateText(
        jsonFormsContext.i18n,
        elementLayout.i18n,
        elementLayout.label
      ) || ''
    );
  }
};

export default React.memo(SteppedLayoutRenderer);
