import { useJsonForms } from '@jsonforms/react';
import React from 'react';
import { Input, Autocomplete, FormRow } from '@dnb/eufemia';
import { ControlProps } from '@jsonforms/core';
import { translationKeys } from '@pm-netbank/translations';
import countries from '../../constants/countries';
import { translateText } from '../../i18n';
import styles from './PhoneInput.module.scss';

const PhoneInputRenderer = (props: ControlProps) => {
  const jsonFormsContext = useJsonForms();

  const { label, handleChange, path, visible, data, errors } = props;

  if (!visible) return null;

  const countriesDropdownData = countries.map((country) => ({
    selected_key: `+${country.code}`,
    selected_value: `+${country.code}`,
    content: `+${country.code} ${country.name}`,
  }));

  const inputData = ((data as string) || '').split(' ');
  const countryCode = inputData[0] || '';
  const phoneNumber = inputData[1] || '';

  return (
    <FormRow
      direction="horizontal"
      label_direction="vertical"
      data-testid={path || 'phone-input'}
      className={styles.phoneInputContainer}
    >
      <Autocomplete
        label={translateText(
          jsonFormsContext.i18n,
          translationKeys.forms.forms_phone_country_code
        )}
        data={countriesDropdownData}
        value={countryCode}
        size="medium"
        data-testid="phone-input-country-code"
        on_change={onCountryCodeChange}
        stretch
      />
      <Input
        data-testid="phone-input-number"
        stretch
        size="medium"
        label={label}
        on_change={onPhoneNumberChange}
        value={phoneNumber}
        status={errors}
        type="tel"
      />
    </FormRow>
  );

  function onCountryCodeChange({ data }: { data: { selected_value: string } }) {
    handleChange(path, `${data?.selected_value || ''} ${phoneNumber}`);
  }

  function onPhoneNumberChange({ value }: { value: string }) {
    handleChange(path, `${countryCode} ${value}`);
  }
};
export default PhoneInputRenderer;
