import { ErrorTranslator } from '@jsonforms/core';
import { ControlElement, JsonFormsI18nState } from '@jsonforms/core';
import { translations } from '@pm-netbank/translations';

interface ControlElementWithErrors extends ControlElement {
  options?: {
    errors?: {
      [key: string]: {
        message?: string;
        i18n?: string;
      };
    };
  };
}

export function getTranslation(
  locale: 'en-GB' | 'nb-NO',
  key: string,
  defaultMessage: string
) {
  const keyParts = key.split('.');
  const lokaliseKey = keyParts[0];

  if (keyParts[1] === 'error') return;
  if (keyParts.length == 1 || keyParts[1] === 'label')
    return translations[locale][lokaliseKey] || defaultMessage;

  return defaultMessage;
}

export function errorTranslator(
  error: Parameters<ErrorTranslator>[0],
  translate: Parameters<ErrorTranslator>[1],
  uischema?: Parameters<ErrorTranslator>[2]
): string {
  const elementSchema = uischema as ControlElementWithErrors;

  if (!elementSchema.options || !elementSchema.options.errors)
    return error.message || error.keyword;

  const errorConfiguration = elementSchema.options.errors[error.keyword];

  if (!errorConfiguration) return error.message || error.keyword;

  return translate(
    errorConfiguration.i18n || '',
    errorConfiguration.message || ''
  );
}

export function translateText(
  i18n?: JsonFormsI18nState,
  translationKey?: string,
  defaultText?: string
) {
  if (!i18n?.translate || !translationKey) return defaultText;

  return i18n.translate(translationKey, defaultText || '');
}
