import React from 'react';
import { Dropdown } from '@dnb/eufemia/components';
import { OwnPropsOfEnum, ControlProps } from '@jsonforms/core';
import BaseFormRow from '../../layouts/BaseFormRow';

const DropdownRenderer = (props: ControlProps & OwnPropsOfEnum) => {
  const { label, handleChange, path, visible, data, errors, options } = props;
  const dropdownOptions = options?.map(({ label, value }) => ({
    content: label,
    value,
  }));

  return (
    <BaseFormRow visible={visible}>
      <Dropdown
        data-testid={path || 'dropdown'}
        stretch
        size="medium"
        value={data ? data : null}
        data={dropdownOptions}
        label={label}
        label_direction="vertical"
        status={errors}
        on_change={onChange}
      />
    </BaseFormRow>
  );

  function onChange({ data }: { data: { content: string; value: string } }) {
    const { value } = data;
    return handleChange(path, value);
  }
};
export default DropdownRenderer;
