import React from 'react';
import {
  Div,
  FormRow,
  Button,
  Input,
  ToggleButton,
  FormStatus,
} from '@dnb/eufemia';
import { ControlProps } from '@jsonforms/core';
import AddByAutocomplete from '../../../AddByAutocomplete/AddByAutocomplete';
import styles from './TaxCountryListControl.module.scss';

interface TaxCountryListControlProps extends ControlProps {
  data: any;
  handleChange(path: string, value: any): void;
  path: string;
}

interface Country {
  name: string;
  disabled: boolean;
  tnid?: string;
}

const TaxCountryList = (props: TaxCountryListControlProps) => {
  const [countries, setCountries] = React.useState<Country[]>([
    { name: 'Norge', disabled: false },
  ]);

  const addCountry = (country) => {
    const countryList = new Set(countries);

    countryList.add({ name: country.content, disabled: false });

    setCountries([...countryList]);
  };

  const disableCountry = (index, disabled) => {
    const newCountries = [...countries];
    newCountries[index].tnid = '';
    newCountries[index].disabled = !disabled;
    setCountries(newCountries);
  };

  const setTnid = (index, tnid) => {
    const newCountries = [...countries];
    newCountries[index].tnid = tnid;
    setCountries(newCountries);
  };

  const { uischema, visible } = props;

  if (!visible) {
    return null;
  }

  const dataSorted = countryMap
    .sort((a, b) => a.localeCompare(b))
    .map((country) => ({
      content: country,
      value: country.toLocaleLowerCase(),
    }));

  return (
    <Div className={uischema?.options?.border && styles.top}>
      {countries.map((country, index) => (
        <FormRow
          direction="horizontal"
          key={index}
          top="x-small"
          bottom="small"
          className={styles.border}
        >
          <div className={styles.taxCountry}>
            <div>
              <ToggleButton variant="checkbox" text={country.name} />
            </div>
            <div className={styles.input}>
              <Input
                right="medium"
                stretch
                size="medium"
                bottom="small"
                label="CPR-nummer"
                placeholder={'DDMMÅÅÅÅ-9999'}
                label_direction="vertical"
                disabled={country.disabled}
                value={country.tnid}
                on_change={({ value }) => {
                  setTnid(index, value);
                }}
              />
              <Button
                className={styles.button}
                right="medium"
                text="Har ikke/husker ikke"
                variant="tertiary"
                on_click={() => {
                  disableCountry(index, country.disabled);
                }}
              />
            </div>
            {country.disabled && (
              <FormStatus
                class={styles.info}
                title="Hover title"
                text="Vi vil kontakte deg for mer informasjon. Du kan fortsette å fylle ut skjemaet nå."
                state="info"
              />
            )}
          </div>
        </FormRow>
      ))}

      <AddByAutocomplete
        buttonText={'Legg til flere land'}
        onAutocompleteChange={addCountry}
        dataList={dataSorted}
      />
    </Div>
  );
};
export default TaxCountryList;

