import React, { useState } from 'react';
import { Form } from '@pm-netbank/forms';
import schema from './configurations/schema.json';
import uiSchema from './configurations/ui-schema';
import { renderers } from './renderers';

function OddForm() {
  const [data, setData] = useState({});

  function onDataChange(data) {
    console.log(data);
    setData(data);
  }

  function handleSave(data, errors) {
    console.log('Save', data, errors);
  }
  return (
    <Form
      schema={schema}
      renderers={renderers}
      uiSchema={uiSchema}
      data={data}
      onDataChange={onDataChange}
      onSave={handleSave}
      data-testid="odd-form"
    />
  );
}

export default OddForm;
