import React from 'react';
import { DatePicker } from '@dnb/eufemia/components';
import { ControlProps } from '@jsonforms/core';
import BaseFormRow from '../../layouts/BaseFormRow';

const DatePickerRenderer = (props: ControlProps) => {
  const { label, handleChange, path, data, errors, visible } = props;

  return (
    <BaseFormRow visible={visible}>
      <DatePicker
        data-testid={path || 'date-picker'}
        stretch
        status={errors}
        size="medium"
        date={data}
        show_input={true}
        label={label}
        label_direction="vertical"
        on_change={onChange}
      />
    </BaseFormRow>
  );

  function onChange({ date }: { date: string }) {
    return handleChange(path, date);
  }
};
export default DatePickerRenderer;
