import { useJsonForms, withJsonFormsMultiEnumProps } from '@jsonforms/react';
import React, { FC, useState } from 'react';
import { ToggleButton } from '@dnb/eufemia';
import {
  ControlProps,
  DispatchPropsOfMultiEnumControl,
  OwnPropsOfEnum,
} from '@jsonforms/core';
import BaseFormRow from '@pm-netbank/forms/src/lib/layouts/BaseFormRow';
import ToggleCollection, {
  CollectionItem,
} from '../../../ToggleCollection/ToggleCollection';

const PropopsalButtonsControl: FC<
  ControlProps & OwnPropsOfEnum & DispatchPropsOfMultiEnumControl
> = ({
  uischema,
  options,
  path,
  handleChange,
  addItem,
  visible,
  data,
  label,
}) => {
  const context = useJsonForms();

  const suggestions = [];
  suggestions.push('norway');
  suggestions.push(context.core.data[uischema.options.watch]);

  const proposals = options.filter((option) => {
    // === norway hardcoded for User testing
    if (Array.isArray(suggestions)) {
      return (
        suggestions.includes(option.value) ||
        option.value === 'norway' ||
        option.value === 'Norge'
      );
    }

    return suggestions === option.value || option.value === 'norway';
  });

  const toggleButtons = proposals
    ?.map((option) => {
      return {
        content: option.label,
        value: option.value,
      };
    })
    .reverse();

  const [items, setItems] = useState<CollectionItem[]>([]);

  const autoCompleteOptions = options
    ?.map((option) => {
      if (!suggestions.includes(option.value)) {
        return {
          content: option.label,
          value: option.value,
        };
      }
    })
    .filter(Boolean);

  const handle = (event) => {
    handleChange(path, event.values);
  };

  const handleNewItem = (item) => {
    const exists = items.find((i) => i.value === item.value);

    if (!exists) {
      setItems([...items, item]);
      addItem(path, item.value);
    }
  };

  return (
    <>
      <BaseFormRow visible={visible}>
        <ToggleButton.Group
          values={data}
          variant={'checkbox'}
          multiselect={true}
          label_direction="vertical"
          layout_direction="row"
          on_change={handle}
        >
          {toggleButtons?.map(({ content, value }) => {
            return (
              <ToggleButton
                text={content}
                key={value}
                value={value}
              ></ToggleButton>
            );
          })}
        </ToggleButton.Group>
      </BaseFormRow>

      <ToggleCollection
        items={items}
        values={data}
        buttonText={label}
        dataList={autoCompleteOptions}
        handleChange={handle}
        handleNewItem={handleNewItem}
      />
    </>
  );
};

export default withJsonFormsMultiEnumProps(PropopsalButtonsControl);
