import React from 'react';
import { RendererProps } from '@jsonforms/core';
import { StepLayout } from '../types';
import { renderChildren } from '../utils';

const StepLayoutRenderer = ({
  schema,
  uischema,
  enabled,
  path,
  visible,
  renderers,
}: RendererProps) => {
  if (!visible) return null;

  const layout = uischema as StepLayout;

  return (
    <React.Fragment>
      {renderChildren(layout, schema, path, enabled, renderers)}
    </React.Fragment>
  );
};

export default React.memo(StepLayoutRenderer);
