import React from 'react';
import { Radio } from '@dnb/eufemia/components';
import { ControlProps, OwnPropsOfEnum } from '@jsonforms/core';
import BaseFormRow from '../../layouts/BaseFormRow';

const RadioGroupRenderer = (props: ControlProps & OwnPropsOfEnum) => {
  const { label, handleChange, path, visible, data, errors, options } = props;

  return (
    <BaseFormRow visible={visible}>
      <Radio.Group
        data-testid={path || 'radio-group'}
        value={data}
        label_direction="vertical"
        layout_direction="column"
        status={errors}
        label={label}
        on_change={onChange}
      >
        {options?.map(({ label, value }, i) => (
          <Radio key={i} label={label} value={value} top="small" />
        ))}
      </Radio.Group>
    </BaseFormRow>
  );

  function onChange({ value }: { value: string }) {
    return handleChange(path, value);
  }
};
export default RadioGroupRenderer;
