import React from 'react';
import { FormRow } from '@dnb/eufemia';
import styles from './BaseFormRow.module.scss';

export interface Props {
  children: React.ReactNode;
  visible?: boolean;
  direction?: 'horizontal' | 'vertical';
}

const BaseFormRow = ({ children, visible, direction = 'vertical' }: Props) => {
  if (!visible) return null;

  return (
    <FormRow
      direction={direction}
      data-testid={`base-form-row-${direction}`}
      className={styles.baseFormRow}
    >
      {children}
    </FormRow>
  );
};

export default BaseFormRow;
