import { JsonFormsContext } from '@jsonforms/react';
import React, { useContext } from 'react';
import { Button } from '@dnb/eufemia/components';
import { ControlProps } from '@jsonforms/core';
import Icon from '../../components/Icon/Icon';
import { FormContext } from '../../context';
import { ButtonControl } from '../types';

const ButtonRenderer = (props: ControlProps) => {
  const { visible, label, uischema } = props;

  const jsonFormsContext = useContext(JsonFormsContext);

  const { dispatch } = useContext(FormContext);

  if (!visible) return null;

  const elementLayout = uischema as ButtonControl;

  return (
    <Button
      data-testid={
        elementLayout.options.action
          ? `button-${elementLayout.options.action}`
          : 'button'
      }
      onClick={onClick}
    >
      <Icon name={elementLayout.options?.icon} />
      {label}
    </Button>
  );

  function onClick() {
    if (!elementLayout.options.action) return;

    dispatch(elementLayout.options.action, jsonFormsContext.core);
  }
};
export default ButtonRenderer;
