import React, { FC } from 'react';
import { ToggleButton } from '@dnb/eufemia';
import AddByAutocomplete, {
  Props as AddByAutocompleteProps,
  DataListItem,
} from '../AddByAutocomplete/AddByAutocomplete';

export type CollectionItem = DataListItem;
export interface Props
  extends Pick<AddByAutocompleteProps, 'dataList' | 'no_animation'> {
  /** List of ToggleButtons to show */
  values?: string[] | any[];
  items?: CollectionItem[];
  /** Label text for add button */
  buttonText: string;
  /** Function to handle changes in the collections, i.e. new items added  */
  handleChange: (value: DataListItem) => void;
  handleNewItem: (item: DataListItem) => void;
}

/**
 * A collection og togglebuttons button to add more items
 */
const ToggleCollecton: FC<Props> = ({
  values = [],
  buttonText,
  dataList,
  handleChange,
  no_animation,
  handleNewItem,
  items,
}) => {
  return (
    <ToggleButton.Group
      values={values}
      variant={'checkbox'}
      multiselect={true}
      label_direction="vertical"
      layout_direction="row"
      on_change={handleChange}
    >
      {items.map(({ content, value }) => (
        <ToggleButton key={value} text={content} value={value} />
      ))}
      <AddByAutocomplete
        buttonText={buttonText}
        dataList={dataList}
        onAutocompleteChange={handleNewItem}
        no_animation={no_animation}
      />
    </ToggleButton.Group>
  );
};

export default ToggleCollecton;
