import { useJsonForms } from '@jsonforms/react';
import React from 'react';
import { P } from '@dnb/eufemia';
import { RendererProps } from '@jsonforms/core';
import classNames from 'classnames';
import Icon from '../../components/Icon/Icon';
import { translateText } from '../../i18n';
import { SectionLayout } from '../types';
import { renderChildren } from '../utils';
import styles from './SectionLayout.module.scss';

const SectionLayoutRenderer = ({
  schema,
  uischema,
  enabled,
  path,
  visible,
  renderers,
}: RendererProps) => {
  const jsonFormsContext = useJsonForms();

  if (!visible) return null;

  const layout = uischema as SectionLayout;
  const { options, i18n, label } = layout;

  return (
    <section className={styles.section}>
      <P
        className={classNames(styles.title, 'dnb-h--large')}
        bottom="small"
        data-testid="section-title"
      >
        <Icon name={options?.icon} color="sea-green" />
        {translateText(jsonFormsContext.i18n, i18n, label)}
      </P>
      <div className={styles.container}>
        {renderChildren(layout, schema, path, enabled, renderers)}
      </div>
    </section>
  );
};

export default React.memo(SectionLayoutRenderer);
