import React from 'react';
import { Checkbox } from '@dnb/eufemia';
import { ControlProps } from '@jsonforms/core';

interface Props extends ControlProps {
  handleChange(path: string, value: boolean): void;
}

const CheckboxRenderer = (props: Props) => {
  const { handleChange, path, visible, label, data, errors } = props;

  if (!visible) return null;

  return (
    <Checkbox
      data-testid={path || 'checkbox'}
      label={label}
      checked={data}
      on_change={onChange}
      status={errors}
    />
  );

  function onChange({ checked }: { checked: boolean }) {
    handleChange(path, checked);
  }
};

export default CheckboxRenderer;
